@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;800;900&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

:root {
    --color-primary: #5a78ff;
    --color-secondary: #0957ff;
}

body{
    position: relative;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

#container {
    padding: 20px;
    width: 100%;
    height: 100vh;
}

.todo{
    position: relative;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 0 auto;
}
.todo_switcher{
    position: absolute;
    right: 20px;
    top:20px;
}
.todo_wrapper{
    min-width: 550px;
    background: rgba( 255, 255, 255);
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.3 );
    backdrop-filter: blur( 3px );
    -webkit-backdrop-filter: blur( 3px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    padding: 20px;
}

.todo_add--input{
    display: flex;
    gap: 10px;
}

.todo-list {
    display: flex;
    flex-direction: column;
    padding: 10px 0;
    &_header{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &_item{
        display: flex;
        flex-direction: column;
        padding: 10px 0;
        border-bottom: 1px solid rgba( 31, 38, 135, 0.3 );
        &--close {
            text-decoration: line-through;
        }
        &--buttons{
            display: flex;
            gap:5px;
            align-self: flex-end;
            margin-top: 5px;
        }
    }
}

.todo-info{
    display: flex;
    align-items: center;
    gap: 5px;
    flex-wrap: wrap;
    & h4{
        margin: 0;
    }
}
//Pagination
.todo_pagination{
    margin-top: 20px;
    margin-left: auto;
}
.pagination>li:first-child>a, .pagination>li:first-child>span,
.pagination>li:last-child>a, .pagination>li:last-child>span {
    border-radius: 0 !important;
}